.element-invisible {
  border: 0 none;
  height: 1px;
  margin: 0;
  overflow: hidden;
  padding: 0;
  position: absolute;
  width: 1px;
}

// Inline turns list items into inline-block
.inline {
  padding-left: 0;
  list-style: none;
  margin-left: -5px;

  > li {
    display: inline-block;
    padding-left: 5px;
    padding-right: 5px;
  }
}

.article-info {
  font-size: 0.9em;
  font-style: italic;
  dd {
    display: inline-block;
    margin-left: 0;
    margin-right: 10px;
    white-space: nowrap;
  }
}

.muted {
  color: @text-muted;
}

.tagspopular {
  ul {
    display: block;
    list-style: outside none none;
    margin: 0;
    overflow: hidden;
    padding: 0;
    > li {
      color: @text-color;
      display: inline-block;
      margin: 0 5px 5px 0;
      > a {
        background: none repeat scroll 0 0 @gray-lighter;
        border-radius: 3px;
        color: @text-color;
        display: block;
        padding: 2px 5px;
        &:hover {
          background: none repeat scroll 0 0 @link-hover-color;
          color: @gray-lighter;
          text-decoration: none;
        }
      }
    }
  }
}

.tagssimilar {
  ul {
    list-style: outside none none;
    margin: 0;
    padding: 0;
    > li {
      border-bottom: 1px solid @hr-border;
      padding: 8px 0;
      > a {
        color: @text-color;
        &:hover {
          color: @link-hover-color;
          text-decoration: none;
        }
      }
    }
  }
}

//
// CATEGORY LIST
// ---------------------------------------------------------
.categories-list {
  margin-bottom: @line-height-computed;
  .clearfix();

  .page-header {
    line-height: normal;
    padding: (@line-height-computed / 2) 5px;
    margin: 0;
    font-size: @font-size-large;
    font-weight: normal;
  }

  // Sub Categories
  .category-item {
    margin-bottom: 0;
    margin-left: @grid-gutter-width;
    .page-header {
      font-size: @font-size-base;
    }
  }
}

.readmore {
  > .btn {
    background-color: @main-color-2;
    color: #ffffff;
    font-size: 12px;
    text-transform: inherit;
    white-space: pre-line;
  }
}

.counter {
  border-radius: 4px;
  display: inline-block;
  margin-top: 20px;
  padding: 6px 0;
}

// Striped
// -------
.list-striped,
.row-striped {
  list-style: none;
  line-height: @line-height-computed;
  text-align: left;
  vertical-align: middle;
  border-top: 1px solid @table-border-color;
  margin-left: 0;
  padding-left: 0;
}

.list-striped li,
.list-striped dd,
.row-striped .row,
.row-striped .row-fluid {
  border-bottom: 1px solid @table-border-color;
  padding: 10px 15px;
}

.list-striped li:nth-child(odd),
.list-striped dd:nth-child(odd),
.row-striped .row:nth-child(odd),
.row-striped .row-fluid:nth-child(odd) {
  background-color: @table-bg-accent;
}

.list-striped li:hover,
.list-striped dd:hover,
.row-striped .row:hover,
.row-striped .row-fluid:hover {
  background-color: @table-bg-hover;
}

.row-striped .row-fluid {
  width: 97%; // lower than 100% since we have padding
}

.row-striped .row-fluid [class*="span"] {
  min-height: 10px;
}

.row-striped .row-fluid [class*="span"] {
  margin-left: 8px;
}

.row-striped .row-fluid [class*="span"]:first-child {
  margin-left: 0;
}

.input-group-btn {
  &:last-child>.btn {
    padding: 8px 14px;
  }
}

//Joomla article
// Joomla Article
.items-leading {
  >div {
    margin-bottom: 2em;
  }
}

.items-row {
  >div {
    margin-bottom: 2em;
  }
}

.item-image {
  img {
    -webkit-border-radius: 10px;
    -moz-border-radius: 10px;
    border-radius: 10px;
  }
}

.blogItem {
  >div {
    &:last-child {
      padding-left: 0;
      padding-right: 0;
    }
  }
  .item-image {
    padding: 0;
    margin-bottom: 2em;
  }
  .articleBody {
    margin-top: 2em;
    border-bottom: 3px solid #eee;
    padding-bottom: 3em;
  }
  .pull-left {
    margin:.5em 2em .5em 0;
  }
  .pull-right {
    margin: .5em 0 1.5em 2em;
  }
  .flex-box {
    margin-bottom: 20px;
  }
}
.tags {
  margin: 0 0 1em;
  li {
    padding-left: 0;
    padding-right: 0;
    a.tag {
      background-color: #eaeaea;
      color: @default-color;
      text-transform: uppercase;
      font-size: 12px;
      padding: 4px 8px;
      font-weight: 700;
    }
  }
}
img {
  max-width: 100%;
}
.page-header {
  border-bottom: none;
  margin: 0;
  padding: 0;
  h1, h2 {
    font-size: 52px;
    margin: 0;
    a {
      color: @heading-color;
      text-decoration: none;
    }
  }
}
.flex-box {
  align-items: center;
  dl {
    margin: 0;
  }
  .icons {
    .btn {
      background: none;
      &:active {
        box-shadow: none;
      }
    }
    .btn-group.open .dropdown-toggle {
      box-shadow: none;
    }
  }
}
.intro {
  margin: 25px 0 55px;
}
.tz-module {
  margin-bottom: 3em;
  .form-control {
    width: 100%;
  }
  h3.module-title {
    text-transform: uppercase;
    color: @heading-color;
    border-top: 2px solid @bordercolor;
    padding-top: 20px;
    margin-top: 0;
    span {
      font-weight: 600;
    }
  }
  .latestnews {
    margin: 0;
    padding: 0;
    li {
      display: table;
      margin-bottom: 1.2em;
      .info {
        time {
          font-size: 12px;
          font-weight: 300;
          color: lighten(@default-color,20%);
        }
        a {
          font-size: 15px;
          display: block;
          line-height: 1.4;
          font-weight: 600;
          color: @heading-color;
        }
      }
    }
  }
  &:hover {
    h3.module-title {
      border-color: @main-color-2;
    }
  }

}

.pager {
  li{
    display: block;
    width: 50%;
    >a {
      border:none;
      padding: 5px 0;
      color: #cccccc;
      &:focus, &:hover {
        background-color: transparent;
      }
      span {
        display: block;
        font-size: 1.2em;
        color: @heading-color;
        line-height: 1.4;
      }
    }
  }
  .previous {
    text-align: left;
    float: left;
  }
  .next {
    text-align: right;
    float: right;
  }
}

.contact {
  h2{
    margin-bottom: 27px;
    margin-top: 0;
  }
  input, textarea {
    border-color: @bordercolor;
    background: #f9f9f9;
    border-radius: 0;
    box-shadow: none;
  }
  button {
    color: #ffffff;
    text-transform: uppercase;
    font-size: 12px;
  }
  .contact-address {
    .contact-head {
      text-transform: uppercase;
      font-weight: 700;
      color: @heading-color;
    }
    dd {
      margin-left: 20px;

    }
    > div {
      margin: 20px 0;
    }
  }
}

.items-row {
  .blogItem {
    >div {
      padding: 0;
    }
  }
  .page-header {
    h2 {
      font-size: 32px;
    }
  }
  .flex-box {
    margin-top: 15px;
  }
  .intro {
    margin: 25px 0 35px;
  }
}

.items-more {
  ol {
    li {
      border-top: 1px solid @bordercolor;
      a {
        padding: 10px 0;
        &:hover {
          border-color: transparent;
        }
      }
    }
  }
  .nav {
    >li>a, .open>a {
      background-color: transparent;
      border-color: transparent;
      &:focus, &:hover {
        background-color: transparent;
        border-color: transparent;
      }
    }
  }
}

.breadcrumb {
  background-color: transparent;
  border-radius: 0;
  padding: 8px 0;
  >li+li {
    &:before {
      content: "";
      padding: 0;
    }
  }

}

.headline {
  text-align: center;
  h1 {
    margin: 80px 0;
  }
}

.tag-category {
  .filters {
    margin-bottom: 2em;
    input {
      padding: 3px 7px;
      font-size: 14px;
    }
  }
  ul.category {
    li {
      padding: 10px 20px;
      border: 1px solid @bordercolor;
      margin-bottom: 2em;
      border-radius: 3px;
      .featuredimg {
        margin-bottom: 1.5em;
      }
      a {
        color: @heading-color;
        text-decoration: none;
      }
    }
  }
}

.login {
  .other-links {
    ul {
      padding: 0;
      list-style: none;
    }
  }
}

.newsfeed {
  ol {
    list-style: none;
    padding: 0;
    li {
      border: 1px solid @bordercolor;
      -webkit-border-radius: 3px;
      -moz-border-radius: 3px;
      border-radius: 3px;
      padding: 5px 20px;
      margin-top: 2em;
    }
  }
}

.search {
  .search-results {
    .result-container {
      border: 1px solid @bordercolor;
      border-radius: 3px;
      padding: 20px;
      margin-top: 2em;
    }
    .result-title {
      a {
        color: @heading-color;
      }
    }
    .result-text {
      margin-top: 1em;
    }
  }
}

.module_menu {
  ul, ol {
    li {
      margin: 0;
    }
  }
}

.latestnews {
  margin: 0;
  padding: 0;
  >li {
    display: flex;
    align-items: center;
    margin-left: -15px;
    margin-right: -15px;
    margin-bottom: 20px;
    &:before, &:after {
      content: " ";
      display: table;
    }
    &:after {
      clear: both;
    }
  }
  .post_title {
    display: block;
    text-transform: uppercase;
  }
  .post_media {
    display: inline-block;
    .post_image {
      background-size: cover;
      background-position: center;
      background-repeat: no-repeat;
      display: block;
      padding-bottom: 110px;
      border-radius: 10px;
    }
    .post_icon {
      height: 110px;
      display: flex;
      width: 100%;
      align-items: center;
      justify-content: center;
      border-radius: 10px;
      font-size: 52px;
      color: #ffffff;
    }
  }
}

.owl-carousel.home-latestnews {
  .owl-item {
    border: none;
    time {
      font-style: italic;
      font-size: 90%;
    }
    .desc {
      margin-top: 10px;
      font-size: 90%;
    }
    &:hover {
      border: none;
    }
  }
}