body {
  font-family: 'EB Garamond', serif;
  font-size: 18px;
  line-height: 1.8;
  color: @default-color;
  background-color: @bg-color;
  font-weight: 400;
}

.h1, .h2, .h3, .h4, .h5, .h6, h1, h2, h3, h4, h5, h6 {
  font-family: 'Poppins', sans-serif;
  font-weight: 700;
  color: @heading-color;
  margin-bottom: 2%;
}

a {
  color: @link-color;
}

p {
  margin-bottom: 1.4em;
}

ol,ul {
  margin-bottom: 1.5em;
}

.flex-box {
  display: flex;
}

blockquote{
  border-left: 5px solid @main-color-2;
}

.blockquote-reverse, blockquote.pull-right {
  border-right: 5px solid @main-color-2;
}

.pagination {
  li {
    margin: 0 5px;
    a {
      border-radius: 20px;
      color: @link-color;
      &:hover {
        color: @main-color-2;
      }
    }
    &:first-child {
      margin-left: 0;
      a {
        border-radius: 20px;
      }
    }
    &:last-child {
      margin-right: 0;
      a {
        border-radius: 20px;
      }
    }
    &.active {
      a {
        background-color: @link-color;
        border-color: @link-color;
        &:hover, &:focus {
          background-color: @main-color-2;
          border-color: @main-color-2;
        }
      }
    }
  }
}

.btn {
  padding: 9px 23px;
  border-radius: 24px;
  text-transform: uppercase;
  &:active, &.active {
    box-shadow: none;
    &:focus, &.focus {
      outline: none;
    }
  }
;
}

pre {
  background-color: #2e2e2e;
  color: #ccc;
}

.form-control {
  border: 1px solid @bordercolor;
  box-shadow: none;
  height: 38px;
  border-radius: 0;
}

.display-flex {
  display: flex;
  align-items: center;
}

.pagination {
  display: flex;
  align-items: center;
  justify-content: space-between;
  >li{
    >a{
      color: @default-color;
      min-width: 38px;
      text-align: center;
      border-color: @bordercolor;
    }
  }
  .active {
    >a {
      background-color: @main-color-1;
      border-color: @main-color-1;
      &:hover, &:focus {
        background-color: @main-color-1;
        border-color: @main-color-1;
      }
    }
  }
  >.disabled{
    >a {
      border-color: @bordercolor;
    }
  }
}

.highlight {
  background: yellow;
}

.btn-group.open .dropdown-toggle {
  box-shadow: none;
  &:focus, &:active {
    box-shadow: none;
  }
}
ul.owl-carousel {
  padding: 0;
}

.btn-primary {
  background-color: @link-color;
  border-color: darken(@link-color,10%);
  &:hover {
    background-color: darken(@link-color, 10%);
    border-color: darken(@link-color,15%);
  }
}
.jsn-uniform .jsn-text-center {
  display: none;
}
.dropdown-menu {
  font-size: inherit;
}
.navbar-collapse {
  padding-left: 0;
  padding-right: 0;
}