.tzpp_bootstrap3 {
  a {
    color: @link-color;
    &:focus, &:hover {
      color: lighten(@link-color, 10%);
    }
  }
  .pagination {
    display: flex;
    li {
      margin: 0 5px;
      a {
        border-radius: 20px;
        color: @link-color;
        &:hover {
          color: @main-color-2;
        }
      }
      &:first-child {
        margin-left: 0;
        a {
          border-radius: 20px;
        }
      }
      &:last-child {
        margin-right: 0;
        a {
          border-radius: 20px;
        }
      }
      &.active {
        a {
          background-color: @link-color;
          border-color: @link-color;
          &:hover, &:focus {
            background-color: @main-color-2;
            border-color: @main-color-2;
          }
        }
      }
    }
  }
}
.tz-extrafields {
  .tz_extrafield-item{
    border-bottom: 1px solid @bordercolor;
    margin-bottom: 0;
  }
}

.tplProfiler {
  a {
    color: @link-color;
  }
  .element .TzInner{
    .profiler-intro {
      .TzPortfolioDescription {
        .TzPortfolioTitle {
          font-family: inherit;
          a {
            color: @heading-color;
          }
        }
        .position {
          color: #7a8386;
        }
        .profiler-social {
          margin-top: 6px;
          a {
            color: #a4a9ab;
          }
        }
        .information {
          padding-top: 15px;
        }
      }
      &:hover {
        .TzPortfolioDescription {
          .TzPortfolioTitle {
            a {
              color: #ffffff;
            }
          }
          .position {
            color: #ffffff;
          }
          .profiler-social {
            a:hover {
              color: #ffffff;
            }
          }
        }
      }
    }
  }
}

.tpItemPage {
  .tpHead {
    .contact{
      a {
        color: @default-color;
      }
    }
    .tpTitle {
      font-size: 37px;
    }
  }
  .tpBody {
    .tpAttachment {
      ul.attachments {
        li {
          margin-bottom: 0;
        }
      }
    }
  }
  .tpArticleExtraField {
    margin-bottom: 20px;
    ul.tpExtraFields {
      margin: 0;
      border: 2px solid @bordercolor;
      li.tp_extrafield-item {
        margin: 0;
        border: none;
        .tp_extrafield-value {
          font-weight: 700;
        }
      }
    }
  }
  .tpSermons {
    margin-bottom: 2em;
  }
  .tpRelated {
    ul.related {
      li {
        margin-bottom: 0;
      }
    }
  }
  .tpArticleAuthor {
    .AuthorAvatar {
      padding: 0;
    }
  }
}

.charity-box {
  background-color: @main-color-2;
  padding: 10px;
  margin: 0;
  color: rgba(255,255,255,0.9);
  h3 {
    margin: 10px 0;
    color: #ffffff;
  }
  .evens {
    .dash {
      border-color: rgba(255,255,255,0.2);
    }
    .dash_title {
      background-color: rgba(255,255,255,0.1);
      border-color: rgba(255,255,255,0.2);
    }
  }
  .donate-progress {
    margin-bottom: 12px;
    .progress-label {
      font-size: 80%;
    }
    .item-progress{
      background-color: rgba(255,255,255,0.25);
      .child-prgb {
        background-color: #ffffff;
      }
    }
  }
  .btn-donate {
    background-color: @link-color;
    border-color: @link-color;
    &:hover, &:focus, &:active {
      background-color: darken(@link-color, 10%);
      border-color: darken(@link-color, 10%);
    }
  }
}
.el-cause-image {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-size: cover;
  background-position: center;
  opacity: 0;
  -webkit-transition: all 0.3s ease 0s;
  -moz-transition: all 0.3s ease 0s;
  -ms-transition: all 0.3s ease 0s;
  -o-transition: all 0.3s ease 0s;
  transition: all 0.3s ease 0s;
}
.el-cause-image.active {
  opacity: 1;
}

.tpp-module-carousel {
  .donate-progress .item-progress {
    background-color: #eeeeee;
    margin-bottom: 10px;
  }
  .TzPortfolioIntrotext {
    margin-bottom: 20px;
  }
}

.tz_stchist_style {
  .owl-carousel .owl-item {
    border: none;
    &:hover {
      border: none;
    }
  }
  .item {
    border: none;
    text-align: center;
    color: #ffffff;
    width: 90%;
    margin: 0 auto;
    h3.title {
      a {
        color: #ffffff;
        font-size: 48px;
      }

    }
    .charity {
      width: 60%;
      margin: 0 auto;
      .progress-ed {
        text-align: left;
      }
    }
    .tpp-module-description {
      margin-top: 30px;
    }
    .tpp-module-readmore {
      margin-top: 30px;
      .btn {
        border-color: @main-color-2;
        background-color: @main-color-2;
        color: #ffffff;
        font-size: inherit;
        padding: 10px 20px;
      }
    }
  }
  .muted {
    color: rgba(255,255,255,0.8);
  }
}
