@media (max-width: 1300px) {
  .sppb-container-inner {
    padding: 0 15px;
  }
}

@media (max-width: @screen-md-max) {
  #tz-header-wrapper {
    #header.row {
      #tz-logo {
        border-bottom: 1px solid @bordercolor;
      }
      #tz-megamenu-area {
        justify-content: flex-start;
        min-height: 50px;
      }
      #tz-position-8 {
        min-height: 50px;
      }
    }
  }
}

@media (max-width: @screen-sm-max) {
  .absolute_block {
    position: relative;
  }
  #tz-top-header-wrapper {
    .sppb-addon-content, .sppb-text-right {
      text-align: center;
    }
  }
  #tz-logo-stack-left-wrapper {
    .sppb-addon-information {
      text-align: center;
    }
    #tz-logo-area {
      justify-content: center;
      margin-bottom: 30px;
    }
    .sppb-text-right {
      text-align: center;
    }
  }
  #tz-header-wrapper {
    #header.row {
      #tz-logo {
        border-color: transparent;
      }
      #tz-megamenu-area {
        justify-content: flex-end;
        min-height: 115px;
      }
      #tz-position-8 {
        min-height: 115px;
      }
      .navbar-collapse {
        width: 100%;
        position: fixed;
        left: 0px;
        background-color: rgb(255, 255, 255);
      }
      .navbar-toggle {
        margin: 0;
        padding: 12px 2px;
      }
    }
    &.Stack-Center {
      #header.row {
        #tz-megamenu-area {
          min-height: 50px;
        }
        #tz-position-8 {
          min-height: 50px;
        }
      }
    }
    &.Stack-Left {
      #header.row {
        #tz-megamenu-area {
          min-height: 50px;
        }
        #tz-position-8 {
          min-height: 50px;
        }

      }
    }
  }
  .items-leading {
    .page-header h2 {
      font-size: 32px;
    }
  }
}

@media (max-width: @screen-xs-max) {
  #tz-header-wrapper {
    #header.row {
      >div {
        min-height: inherit;
      }
      #tz-logo {
        border-bottom: 1px solid @bordercolor;
        padding: 20px;
      }
      #tz-megamenu-area {
        justify-content: flex-start;
        min-height: 50px;
      }
      #tz-position-8 {
        min-height: 50px;
      }
      .navbar-toggle {
        float: left;
        margin: 0;
        padding: 12px 2px;
      }
    }
  }
  #tz-footer-bottom-wrapper {
    .sppb-text-right {
      text-align: left;
    }
  }

  .pagination {
    flex-direction: column;
    >.pagination {
      flex-direction: row;
    }
    >.counter {
      margin-bottom: 0;
      margin-top: 10px;
    }
  }
  .latestnews > li {
    flex-direction: column;
  }
}