.padding-lr-0 {
  padding-left: 0;
  padding-right: 0;
}
#tz-top-header-wrapper {
  background-color: @main-color-1;
  color: #ffffff;
  a {
    color: #ffffff;
  }
  .tz-module {
    margin: 0;
  }
  .sppb-btn-custom, .sppb-column-addons {
    line-height: 1;
  }
}

#tz-logo-wrapper {
  text-align: center;
  padding: 35px 0;
}

#tzlogo {
  line-height: 1;
  z-index: 1;
}

#tz-logo-stack-left-wrapper {
  padding: 30px 0;
  .tz-module {
    margin: 0;
    width: 100%;
  }
  #logo-stack-left.row {
    position: relative;
    >div {
      display: flex;
      align-items: center;
      min-height: 50px;
      &:last-child {
        justify-content: flex-end;
      }
      .mod-sppagebuilder {
        width: 100%;
      }
    }
  }
}

#tz-header-wrapper {
  #header.row {
    position: relative;
    >div {
      min-height: 115px;
      display: flex;
      align-items: center;
      &:last-child {
        justify-content: flex-end;
      }
      .mod-sppagebuilder {
        width: 100%;
        .sppb-row {
          justify-content: flex-end;
        }
      }
    }
  }
  .navbar {
    margin: 0;
  }
  #tz-megamenu-area {
    justify-content: flex-end;
    z-index: 2;
  }
  .tz-module {
    margin: 0;
  }
  .search-button {
    color: @main-color-1;
    font-size: 16px;
  }
  #tz-position-4 {
    -webkit-transition: all 0.3s ease 0s;
    -moz-transition: all 0.3s ease 0s;
    -ms-transition: all 0.3s ease 0s;
    -o-transition: all 0.3s ease 0s;
    transition: all 0.3s ease 0s;
    position: absolute;
    top: 0;
    right: 0;
    opacity: 0;
    background-color: #ffffff;
    .tz-module {
      width: 100%;
      position: relative;
      input[type=search] {
        width: 100%;
        border: none;
        font-size: 150%;
        &:focus {
          border: none;
          outline: none;
        }
      }
      .close-searchbox {
        position: absolute;
        top: 20%;
        right: 0;
      }
    }
    .search-button {
      margin-left:20px;
    }
    &.search-box-show {
      -webkit-transition: all 0.3s ease 0s;
      -moz-transition: all 0.3s ease 0s;
      -ms-transition: all 0.3s ease 0s;
      -o-transition: all 0.3s ease 0s;
      transition: all 0.3s ease 0s;
      opacity: 1;
      z-index: 2;
    }
  }
  #tz-position-8 {
    justify-content: flex-end;
    z-index: 1;
    >* {
      margin-right: 20px;
      &:last-child {
        margin-right: 0;
      }
    }
  }
  .mod-languages {
    a {
      line-height: 1;
      img {
        vertical-align: baseline;
      }
    }
  }
  .sppb-addon-gbutton {
    a {
      color: @heading-color;
    }
  }
  &.Classic {
    .sticky {

    }
  }
  &.Stack-Center {
    border-top: 1px solid @bordercolor;
    #header.row {
      >div {
        min-height: 50px;
        &#tz-megamenu-area {
          justify-content: center;
        }
      }
    }
  }
  &.Stack-Left {
    border-top: 1px solid @bordercolor;
    #header.row {
      >div {
        min-height: 50px;
        &#tz-megamenu-area {
          justify-content: flex-start;
        }
      }
    }
    #tz-position-4 {
      background-color: @bg_option2;
      .tz-module input[type=search] {
        background-color: @bg_option2;
      }
    }
  }
  .sticky {
    -webkit-transition: all 0.2s ease 0s;
    -moz-transition: all 0.2s ease 0s;
    -ms-transition: all 0.2s ease 0s;
    -o-transition: all 0.2s ease 0s;
    transition: all 0.2s ease 0s;
    position: fixed;
    top: 0;
    z-index: 999;
  }
}

#tz-menu-wrapper {
  background-color: @main-color-2;
  color: #ffffff;
  >div {
    -webkit-transition: all 0.2s ease 0s;
    -moz-transition: all 0.2s ease 0s;
    -ms-transition: all 0.2s ease 0s;
    -o-transition: all 0.2s ease 0s;
    transition: all 0.2s ease 0s;
  }
  .navbar-nav>li>a {
    color: #ffffff;
  }

}

#tz-breadcrumb-wrapper {
  .tz-module {
    color: @default-color;
    margin: 0;
    h2 {
      font-size: 40px;
      margin: 0 0 10px;
      color: @heading-color;
    }
    .breadcrumb {
      padding-bottom: 0;
      margin-bottom: 5px;
      a {
        color: @default-color;
      }
      .active {
        color: @link-color;
      }
    }
  }
}

#tz-position-7 {
  .module_menu {
    .nav {
      >li {
        margin: 0 0 1px;
        >a {
          padding: 10px 15px;
          color: @default-color;
          background-color: @bg_option2;
          border-radius: 0;
          &:hover, &:focus {
            background-color: @main-color-2;
            color: #ffffff;
          }
        }
        &.active {
          >a {
            background-color: @bg_option2;
            color: @link-color;
            &:hover, &:focus {
              background-color: @main-color-2;
              color: #ffffff;
            }
          }
        }

      }
    }
  }
}

#tz-footer, #tz-footer-top-wrapper, #tz-footer-bottom-wrapper {
  .tz-module {
    margin: 0;
  }
  table.acymailing_form {
    input[type=text] {
      background-color: rgba(0,0,0,0.1);
      border: 1px solid rgba(255,255,255,0.1);
      color: rgba(255,255,255,0.4);
    }
  }
  .sppb-addon-social-share ul li a {
    font-size: 18px;
    color: rgba(255,255,255,0.7);
    &:hover {
      color: rgba(255,255,255,1);
    }
  }
}

#tz-footer {
  color: rgba(255,255,255,0.7);
  h1,h2,h3,h4,h5,h6 {
    color: #ffffff;
  }
  ul.nav {
    li {
      a {
        padding: 0;
        color: rgba(255,255,255,0.7);
        &:focus, &:hover {
          background-color: transparent;
          color: rgba(255,255,255,1);
        }
      }
      &.open {
        a {
          background-color: transparent;
          &:focus, &:hover {
            background-color: transparent;
            border-color: transparent;
          }
        }
      }
      &.active {
        a {
          color: rgba(255,255,255,1);
          background-color: transparent;
          &:focus, &:hover {
            background-color: transparent;
            border-color: transparent;
          }
        }
      }
    }
  }
}

#tz-footer-bottom-wrapper {
  color: @bottom-color;
  ol, ul {
    li {
      margin-bottom: 0;
    }
  }
  ul.nav {
    li {
      margin-left: 20px;
      display: inline-block;
      a {
        padding: 0;
        color: rgba(255,255,255,0.4);
        line-height: inherit;
        &:focus, &:hover {
          background-color: transparent;
          color: rgba(255,255,255,1);
        }
      }
      &.open {
        a {
          background-color: transparent;
          &:focus, &:hover {
            background-color: transparent;
            border-color: transparent;
          }
        }
      }
      &.active {
        a {
          color: rgba(255,255,255,1);
          background-color: transparent;
          &:focus, &:hover {
            background-color: transparent;
            border-color: transparent;
          }
        }
      }
      &:first-child {
        margin-left: 0;
      }
    }
  }
}
.sppb-testimonial-pro {
  .fa-quote-right {
    color: @link-color;
    font-size: 60px;
    margin-bottom: 40px;
    position: absolute;
    right: 20px;
    bottom: 5px;
  }
  .sppb-testimonial-message {
    color: @default-color;
    font-weight: 400;
    font-size: 20px;
    line-height: 1.5;
    font-style: italic;
    background-color: #ffffff;
    border-radius: 10px;
    border: 1px solid #ebebeb;
    padding: 20px 25px;
    text-align: left;
  }
  .sppb-addon-testimonial-pro-footer {
    font-size: 24px;
    color: @heading-color;
    padding-left: 20px;
    img {
      padding: 4px;
      background-color: #ffffff;
      border: 1px solid @bordercolor;
    }
  }
  .sppb-addon-testimonial-pro-client-url {
    font-size: 20px;
    &:before {
      content: "- ";
    }
  }
  .sppb-carousel-indicators li {
    border-color: rgba(0,0,0,0.1);
    &.active {
      border-color: @link-color;
      background-color: @link-color;
    }
  }
}
.sppb-row-overlay {
  z-index: 1;
}
#tz-position-13 {
  .tz-module {
    margin: 0;
  }
}

.sppb-addon-clients {
  img {
    padding: 30px 0;
  }
}

.absolute_block {
  position: absolute;
  .sppb-addon-single-image-container {
    background-color: #ffffff;
  }
  &.sppb-addon-gmap {
    background-color: #ffffff;
  }
  width: 100%;
  img {
    padding: 20px;
  }
  .sppb-addon-gmap-canvas {
    margin: 20px;
  }
}
.help-menu-heading {
  text-align: center;
  .nav>li>a, .nav>li.open>a {
    font-size: 210%;
    color: @heading-color;
    font-weight: 600;
    background-color: transparent;
    padding: 0;
    &:hover {
      color: @link-color;
      background-color: transparent;
    }
    &:focus {
      outline: none;
      background-color: transparent;
    }
  }
}
.sppb-addon-clients {
  font-size: 22px;
  font-weight: 600;
}

.sppb-panel-modern>.sppb-panel-heading {
  padding: 10px 30px 10px 15px;
}
.sppb-panel-title {
  font-size: 18px;
}