.main-container {
  max-width: 1300px;
  margin: 0 auto;
  -webkit-box-shadow: 0 0 8px rgba(0, 0, 0, 0.07);
  -moz-box-shadow: 0 0 8px rgba(0, 0, 0, 0.07);
  box-shadow: 0 0 8px rgba(0, 0, 0, 0.07);
  background-color: @bg_default;
}
#tz-menu-wrapper {
  #plazart-mainnav {
    margin: 0;
    border: none;
    .navbar-collapse {
      padding: 0;
    }
  }
}

.tz-module {
  .nav > {
    li > {
      a {
        display: block;
        padding: 5px 10px;
      }
    }
  }
}

.form-group {
  margin-bottom: 20px;
}

#login-form ul.unstyled {
  list-style: none;
  margin-top: 20px;
  padding: 0;
}

.module-title {
  margin-bottom: 30px;
  font-size: 22px;
}

.framework-logo {
  padding: 10px 0 30px;
}

.white {
  color: #ffffff;
  .h1, .h2, .h3, .h4, .h5, .h6, h1, h2, h3, h4, h5, h6 {
    color: #FFFFFF;
  }
}

table.acymailing_form {
  width: 100%;
  input[type=text] {
    min-height: 48px;
    border-radius: 24px;
    padding: 0 23px;
    border: 1px solid @bordercolor;
    &:focus {
      outline: none;
    }
  }
}