.jsn-uniform.jsn-master {
  .jsn-bootstrap {
    .form-actions {
      padding: 0;
    }
    .jsn-form-content {
      .control-group {
        padding: 0;
        margin-bottom: 20px;
        input[type=text], textarea {
          padding: 12px 20px;
          border-radius: 24px;
          border: 1px solid @bordercolor;
          width: 100%;
          -webkit-box-sizing: border-box;
          -moz-box-sizing: border-box;
          box-sizing: border-box;
          &::placeholder {
            color: #a2a7a9;
          }
        }
      }
    }
  }
}